<template>
  <div class="dslfp">
    <div>
      <el-select v-model="filterLatest.host_group"
                 class="filterLatestInput"
                 size="small"
                 multiple
                 filterable
                 :allow-create="false"
                 default-first-option
                 placeholder="主机群组">
        <el-option v-for="item in groupOptions"
                   :key="item.value"
                   :label="item.name"
                   :value="item.groupid">
        </el-option>
      </el-select>
      <el-select v-model="filterLatest.host"
                 class="filterLatestInput"
                 size="small"
                 multiple
                 filterable
                 :allow-create="false"
                 default-first-option
                 placeholder="主机">
        <el-option v-for="item in hostOptions"
                   :key="item.value"
                   :label="item.name"
                   :value="item.hostid">
        </el-option>
      </el-select>
      <el-input size="small"
                class="filterLatestInput"
                placeholder="名称"
                suffix-icon="el-icon-plus"
                v-model="filterLatest.itemame"></el-input>
      <el-button size="small"
                 type="primary"
                 icon="el-icon-plus"
                 @click="filterLatestResource">搜索</el-button>
    </div>
    <div class="latestData">
      <el-table :data="latestDataList"
                @selection-change="handleSelectionChange"
                size="small"
                border
                stripe
                style="width: 100%; font-size: 10px">
        <el-table-column type="selection"
                         width="40"> </el-table-column>

        <el-table-column prop="hosts.0.name"
                         show-overflow-tooltip
                         label="主机"
                         width="180"></el-table-column>
        <el-table-column prop="name"
                         label="名称">
          <template slot-scope="itemInfo">
            <el-tooltip placement="top"
                        v-if="itemInfo.row.description != ''">
              <div slot="content">{{ itemInfo.row.description }}</div>
              <p style="
                  overflow: hidden;
                  width: 100%;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                ">
                {{ itemInfo.row.name }}
              </p>
            </el-tooltip>
            <p v-else
               style="
                overflow: hidden;
                width: 100%;
                white-space: nowrap;
                text-overflow: ellipsis;
              ">
              {{ itemInfo.row.name }}
            </p>
          </template>
        </el-table-column>
        <el-table-column prop="lastclock"
                         label="最近检查记录"
                         width="150"></el-table-column>
        <el-table-column prop="tagsvalue"
                         label="标记"
                         show-overflow-tooltip
                         width="240"></el-table-column>
        <el-table-column prop="itemid"
                         label="动态图"
                         width="100">
          <template slot-scope="latestDataitemid">
            <el-button @click="latestDynamicData(latestDataitemid.row.itemid)"
                       type="text"
                       size="small">动态图</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-button size="small"
                 style="margin-top: 10px; color: #1989fa; display: inline-block"
                 :disabled="this.multipleSelection.length == 0"
                 @click="dynameicMany">批量动态图</el-button>
      <div style="
          display: inline-block;
          float: right;
          margin-right: 0px;
          margin-top: 5px;
          justify-content: flex-end;
        ">
        <el-pagination background
                       @current-change="currentChange"
                       @size-change="sizeChange"
                       layout="sizes, prev, pager, next, jumper, ->, total"
                       :total="total">
        </el-pagination>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'mon_latest_data',
  data() {
    return {
      filterdata: '',
      filterLatest: {
        host_group: '',
        host: '',
        itemame: '',
      },
      latestData: [],
      multipleSelection: [],
      groupOptions: [],
      hostOptions: [],
      hostid: this.$route.query.hostid,
      latestDataList: [],
      latestDataPage: 1,
      total: 0,
      latestDataSize: 10,
    }
  },
  created() {
    this.filterLatest.host = this.$route.query.hostid
  },
  mounted() {
    // 初始化
    this.getLatestData()
    // 主机
    this.initHostList()
    // 主机群组
    this.initGroupList()
  },
  methods: {
    dynameicMany() {
      let itemids = ''
      this.multipleSelection.forEach((item) => {
        itemids += item.itemid + ','
      })
      this.$router.push({
        path: '/monitor/mon_dynamic_data',
        query: { itemid: itemids },
      })
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    // 组合搜索
    filterLatestResource() {
      this.filterdata = ''
      this.latestDataList = []
      this.latestDataPage = 1
      if (this.filterLatest.host_group != '') {
        this.filterdata =
          this.filterdata + '/host_group/' + this.filterLatest.host_group
      }
      if (this.filterLatest.host != '') {
        this.filterdata = this.filterdata + '/hostid/' + this.filterLatest.host
      }
      if (this.filterLatest.itemame != '') {
        this.filterdata =
          this.filterdata + '/itemame/' + this.filterLatest.itemame
      }
      this.getLatestData()
    },
    // 跳转页面
    latestDynamicData(itemid) {
      this.$router.replace('/monitor/mon_dynamic_data?itemid=' + itemid)
    },
    // 主机群组
    initGroupList() {
      this.getRequest('/home/Monitor/groupselect').then((groupList) => {
        if (groupList.code == 200) {
          groupList.data.forEach((index) => {
            this.groupOptions.push(index)
          })
        } else {
          this.$message.error(resp.error)
          return
        }
      })
    },
    // 主机
    initHostList() {
      this.getRequest('/home/Monitor/hostselect').then((serverList) => {
        if (serverList.code == 200) {
          serverList.data.forEach((index) => {
            this.hostOptions.push(index)
          })
        } else {
          this.$message.error(resp.error)
          return
        }
      })
    },
    // 每页条数
    sizeChange(currentSize) {
      this.latestDataSize = currentSize
      this.getLatestData()
    },
    // 当前页  	currentPage 改变时会触发
    currentChange(currenPage) {
      this.latestDataPage = currenPage
      this.getLatestData()
    },
    // 初始化
    getLatestData() {
      this.getRequest(
        '/home/Monitor/mon_latest_data/hostid/' + this.hostid
      ).then((resp) => {
        if (resp.code == 200) {
          this.total = resp.itemNumber
          this.latestDataList = []
          let itemdata = []
          for (let i = 0; i < resp.data.length; i++) {
            itemdata[i + 1] = resp.data[i]
          }
          let maxlength =
            this.latestDataPage * this.latestDataSize > resp.itemNumber
              ? resp.itemNumber
              : this.latestDataPage * this.latestDataSize

          // 所有列表
          for (
            let i =
              this.latestDataPage == 1
                ? this.latestDataPage
                : this.latestDataPage * this.latestDataSize -
                  this.latestDataSize +
                  1;
            i <= maxlength;
            i++
          ) {
            this.latestDataList.push(itemdata[i])
          }
        }
      })
    },
  },
}
</script>

<style>
.dslfp {
  background-color: #fff;
}
.filterLatestInput {
  width: 300px;
  margin: 10px 8px 8px 0px;
}

.latestData {
  margin-top: 15px;
}
.el-tooltip__popper {
  max-width: 50%;
}
</style>
