<template>
  <div class="dslfp">
    <div class="dash_board">
      <el-tag style="
          background-color: #86cc890d;
          border-color: #86cc89;
          color: #86cc89;
        "><span>{{ normalNumber }} <br />
          监控主机数总数</span></el-tag>
      <el-tag style="background-color: #fef0f0; border-color: #f56c6c; color: #f56c6c"><span>{{ errorNumber }} <br />
          监控不可用主机数</span></el-tag>
      <el-tag style="background-color: #f4f4f5; border-color: #909399; color: #909399"><span>{{ serverNumber }} <br />
          监控主机数总数</span>
      </el-tag>
      <el-tag style="
          background-color: #f56c6c26;
          border-color: #e6a23c;
          color: #e6a23c;
        "><span>{{ alertNumber }} <br />
          警报数</span>
      </el-tag>
    </div>
    <div>
      <el-table :row-class-name="tableRowClassName"
                :data="dashBoardData"
                style="width: 100%; border-radius: 5px;">
        <el-table-column prop="clock_time"
                         label="日期"
                         width="170">
        </el-table-column>
        <el-table-column prop="hosts_name"
                         label="主机"
                         show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="name"
                         label="问题 • 严重性"
                         show-overflow-tooltip>
          <template slot-scope="scopeseverity">
            {{ scopeseverity.row.severity }}--
            {{ scopeseverity.row.name }}
          </template>
        </el-table-column>
        <el-table-column prop="continued_time"
                         label="持续时间"
                         width="120"></el-table-column>
        <el-table-column prop="acknowledged_status"
                         label="确认"
                         width="50"></el-table-column>
        <el-table-column prop="tags"
                         label="标记"
                         show-overflow-tooltip></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'mon_dash_board',
  data() {
    return {
      dashBoardData: [],
      normalNumber: '',
      errorNumber: '',
      serverNumber: '',
      alertNumber: '',
    }
  },
  mounted() {
    this.initDashBoardData()
  },
  methods: {
    tableRowClassName({ row }) {
      if (row.severity == 0) {
        return 'classified-row'
      } else if (row.severity == 1) {
        return 'information-row'
      } else if (row.severity == 2) {
        return 'warning-row'
      } else if (row.severity == 3) {
        return 'average-row'
      } else if (row.severity == 4) {
        return 'high-row'
      } else if (row.severity == 5) {
        return 'disaster-row'
      }
      return ''
    },
    initDashBoardData() {
      this.getRequest('/home/Monitor/mon_dash_board').then((resp) => {
        if (resp) {
          console.log(resp)
          this.dashBoardData = resp.event_list
          this.serverNumber = resp.serverNumber
          this.normalNumber = resp.normalNumber
          this.errorNumber = resp.errorNumber
          this.alertNumber = resp.alertNumber
        }
      })
    },
  },
}
</script>

<style>
.dslfp {
  background-color: #fff;
}

.dash_board .el-tag {
  width: 150px;
  height: 100px;
  line-height: 20px;
  text-align: center;
  padding-top: 25px;
  margin: 10px 10px 10px 0;
}

.el-table__body .classified-row {
  background: #b5b5b5;
}

.el-table__body .information-row {
  background: #63b8ff;
}

.el-table__body .warning-row {
  background: #ffc859;
}

.el-table__body .average-row {
  background: #ffa059;
}

.el-table__body .high-row {
  background: #e97659;
}

.el-table__body .disaster-row {
  background: #e45959;
}
</style>
