<template>
  <div class="zhuti">
    <div class="nav">
      <el-input size="small"
                class="filterServerInput"
                placeholder="名称"
                suffix-icon="el-icon-plus"
                v-model="filterServer.name"></el-input>

      <el-select v-model="filterServer.host_group"
                 class="filterServerInput"
                 size="small"
                 multiple
                 filterable
                 :allow-create="false"
                 default-first-option
                 placeholder="主机群">
        <el-option v-for="item in groupOptions"
                   :key="item.value"
                   :label="item.name"
                   :value="item.groupid">
        </el-option>
      </el-select>
      <el-input size="small"
                class="filterServerInput"
                placeholder="ip"
                suffix-icon="el-icon-plus"
                v-model="filterServer.ip"></el-input>
      <el-input size="small"
                class="filterServerInput"
                placeholder="端口"
                suffix-icon="el-icon-plus"
                v-model="filterServer.port"></el-input>

      <div size="small"
           class="filterServerInput">
        严重性:
        <el-checkbox-group style="display: initial"
                           size="small"
                           class="filterServerInputs"
                           v-model="filterServer.severity">
          <el-checkbox label="0">未分类</el-checkbox>
          <el-checkbox label="1">信息</el-checkbox>
          <el-checkbox label="2">警告</el-checkbox>
          <el-checkbox label="3">一般严重</el-checkbox>
          <el-checkbox label="4">严重</el-checkbox>
          <el-checkbox label="5">灾难</el-checkbox>
        </el-checkbox-group>
      </div>
      <div size="small"
           class="filterServerInput">
        状态:
        <el-radio-group style="display: initial"
                        size="small"
                        class="filterServerInput"
                        v-model="filterServer.available">
          <el-radio-button label="2">任何</el-radio-button>
          <el-radio-button label="1">启用的</el-radio-button>
          <el-radio-button label="0">停用的</el-radio-button>
        </el-radio-group>
      </div>
      <el-button size="small"
                 type="primary"
                 icon="el-icon-plus"
                 class="search"
                 @click="filterServerResource">搜索</el-button>
    </div>
    <div class="serManaMain">
      <!-- 主机列表数据 -->
      <el-table :data="serverList"
                size="small"
                border
                stripe
                style="width: 100%">
        <el-table-column prop="name"
                         show-overflow-tooltip
                         label="名称"></el-table-column>
        <el-table-column prop="ip_port"
                         show-overflow-tooltip
                         label="接口"></el-table-column>
        <el-table-column prop="timeTags"
                         show-overflow-tooltip
                         label="标记">
        </el-table-column>
        <el-table-column prop="interfacesAvailable"
                         label="可用性"
                         width="80">
          <template slot-scope="scoopeinterfaces">
            <el-tag v-if="scoopeinterfaces.row.interfacesAvailable == '1'"
                    type="success">{{ scoopeinterfaces.row.interfacesType }}</el-tag>
            <el-tag v-else-if="scoopeinterfaces.row.interfacesAvailable == '2'"
                    type="danger">{{ scoopeinterfaces.row.interfacesType }}</el-tag>
            <el-tag v-else
                    type="info">{{
              scoopeinterfaces.row.interfacesType
            }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="interfacesAvailable"
                         label="状态"
                         width="80">
          <template slot-scope="scopeAvailable">
            <el-tag v-if="scopeAvailable.row.interfacesAvailable == '1'"
                    type="success">已启用</el-tag>
            <el-tag v-else-if="scopeAvailable.row.interfacesAvailable == '2'"
                    type="success">已启用</el-tag>
            <el-tag v-else
                    type="danger">未启用</el-tag>
          </template>
        </el-table-column>

        <el-table-column prop="hostid"
                         label="最新数据"
                         width="80">
          <template slot-scope="scopeLatestData">
            <el-button @click="latestData(scopeLatestData.row.hostid)"
                       type="text"
                       size="small">最新数据</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="display: flex; justify-content: flex-end">
        <el-pagination background
                       @current-change="currentChange"
                       @size-change="sizeChange"
                       layout="sizes, prev, pager, next, jumper, ->, total"
                       :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'mon_ser_list',
  data() {
    return {
      groupOptions: [],
      filterdata: '',
      filterServer: {
        name: '',
        host_group: '',
        ip: '',
        port: '',
        severity: [],
        available: '2',
      },
      serverList: [], // 全局主机列表数据
      serverResource: [],
      serverPage: 1, //页数
      serverSize: 10, //页面显示条数
      total: 0, //总条目数
      ServerListUrl: '/home/Monitor/mon_ser_list',
    }
  },
  mounted() {
    this.initGroupList()
    this.getServerResource()
  },
  methods: {
    // 页面跳转
    latestData(hostid) {
      this.$router.replace('/monitor/mon_latest_data?hostid=' + hostid)
    },
    // 组合搜索
    filterServerResource() {
      this.filterdata = ''
      this.serverList = []
      this.serverPage = 1
      if (this.filterServer.name != '') {
        this.filterdata = this.filterdata + '/name/' + this.filterServer.name
      }
      if (this.filterServer.host_group != '') {
        this.filterdata =
          this.filterdata + '/host_group/' + this.filterServer.host_group
      }
      if (this.filterServer.ip != '') {
        this.filterdata = this.filterdata + '/ip/' + this.filterServer.ip
      }
      if (this.filterServer.port != '') {
        this.filterdata = this.filterdata + '/port/' + this.filterServer.port
      }
      if (this.filterServer.severity != '') {
        this.filterdata =
          this.filterdata + '/severity/' + this.filterServer.severity
      }
      this.filterdata =
        this.filterdata + '/available/' + this.filterServer.available
      // console.log(this.filterdata)
      this.getServerResource()
    },
    // 每页条数
    sizeChange(currentSize) {
      this.serverSize = currentSize
      this.getServerResource()
    },
    // 当前页  	currentPage 改变时会触发
    currentChange(currenPage) {
      this.serverPage = currenPage
      this.getServerResource()
    },
    // 主机群
    initGroupList() {
      this.getRequest('/home/Monitor/groupselect').then((groupList) => {
        if (groupList.code == 200) {
          groupList.data.forEach((index) => {
            this.groupOptions.push(index)
          })
        } else {
          this.$message.error(resp.error)
          return
        }
      })
    },
    // 初始化主机列表
    getServerResource() {
      // url = 接口 +  当前选中
      let url = this.ServerListUrl + this.filterdata

      if (this.serverResource.length <= 1) {
        // 从后端获取的主机列表数据 resp.data为列表数据
        this.getRequest(url).then((resp) => {
          if (resp) {
            this.total = resp.serNumber
            this.serverList = []
            let serdata = []
            for (let i = 0; i < resp.data.length; i++) {
              serdata[i + 1] = resp.data[i]
            }
            let maxlength =
              this.serverPage * this.serverSize > resp.serNumber
                ? resp.serNumber
                : this.serverPage * this.serverSize
            // 所有列表
            for (
              let i =
                this.serverPage == 1
                  ? this.serverPage
                  : this.serverPage * this.serverSize - this.serverSize + 1;
              i <= maxlength;
              i++
            ) {
              // console.log(i)
              this.serverList.push(serdata[i])
            }

            // console.log(this.serverList)
            console.log(resp)
          }
        })
      }
    },
  },
}
</script>

<style>
.zhuti {
  width: 100%;
  height: 100%;
  background-color: white;
  opacity: 0.9;
  border-radius: 3px;
  margin-top: 10px;
  box-shadow: 2px 2px 2px #fff;
  padding: 10px;
}
.filterServerInput {
  width: 300px;
  margin: 10px 10px 0 0;
}

.search {
  float: right;
  margin: 10px 0px 10px 0px;
}
</style>
